export default "@import '../../../../themes/src/default.css';\n@import '../../../../toast-plate/src/vars.css';\n\n:root {\n    --toast-box-shadow: var(--shadow-m);\n}\n\n.toastPlate {\n    box-shadow: var(--toast-box-shadow);\n    word-break: break-word;\n}\n\n.title {\n    font-weight: 400;\n}\n\n.popoverInner {\n    border: none;\n    border-radius: var(--toast-plate-border-radius);\n    box-shadow: var(--toast-box-shadow);\n    background-color: transparent;\n}\n\n.block {\n    width: 100%;\n}\n\n.fixed {\n    position: fixed;\n    bottom: var(--gap-2xl);\n\n    &.enter {\n        opacity: 0;\n        transform: scale(0.98);\n    }\n\n    &.enterActive {\n        opacity: 1;\n        transform: scale(1);\n        transition: opacity 150ms ease-in-out, transform 150ms ease-in-out;\n    }\n\n    &.exit {\n        opacity: 1;\n        transform: scale(1);\n    }\n\n    &.exitActive {\n        opacity: 0;\n        transform: scale(0.98);\n        transition: opacity 150ms ease-in-out, transform 150ms ease-in-out;\n    }\n}\n";